<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
            <!--  -->
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getOrderList" @keyup.enter.native="getOrderList">
            <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="orderlist" border stripe>
          <el-table-column type="index"></el-table-column>
          <el-table-column label='' prop="order_number"></el-table-column>
          <el-table-column label='' prop="order_price"></el-table-column>
          <el-table-column label='' prop="pay_status">
              <template slot-scope="scope">
                 <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
                 <el-tag type="danger" v-if="scope.row.pay_status === '0'"></el-tag>
              </template>
          </el-table-column>
          <el-table-column label='' prop="is_send"></el-table-column>
          <el-table-column label='' prop="create_time">
              <template slot-scope="scope">
                  {{scope.row.create_time | dataForm}}
              </template>
          </el-table-column>
          <el-table-column label='' >
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox(scope.row.order_id)"></el-button>
                <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox"></el-button> 
              </template>
              
          </el-table-column>

      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tokal"
        background>
    </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" label-width='100px' @close='addreeFormClose'>
            <el-form :model="addreeForm" :rules="addreeFormRules" ref="addreeFormRef" >
                <el-form-item label="/" prop="addree1">
                       <el-cascader
                        v-model="addreeForm.addree1"
                        :options="cityData"
                        :props="{ expandTrigger: 'hover' }"
                        @change="handleChange"></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="addree2">
                    <el-input v-model="addreeForm.addree2" ></el-input>
                </el-form-item>
            </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editForm"> </el-button>
        </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
          {{activity.context}}
          {{activity.timestamp}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
import cityData from './citydata'
import progressInfo from './progressInfo'
export default {
  data() {
    return {
        queryInfo:{
            query:'',
            pagenum:1,
            pagesize:10
        },
        orderlist:[],
        tokal:0,
        editDialogVisible:false,
        addreeForm:{
            addree1:[],
            addree2:''
        },
        addreeFormRules:{
            addree1:[
                 { required: true, message: '', trigger: 'blur' }
            ],
            addree2:[
                 { required: true, message: '', trigger: 'blur' }
            ]
        },
        cityData,
        progressVisible: false,
        progressInfo: []
    }
  },
  created() {
      this.getOrderList()
  },
  methods: {
      async getOrderList(){
          const {data:res} = await this.$http.get(`orders`, { params:this.queryInfo} )
          if(res.meta.status !== 200) {
              return this.$message.error("!");
          }
          this.$message.success('!')
          this.orderlist = res.data.goods
          this.tokal = res.data.total
        //   console.log(res)
      },
      handleSizeChange(newSize){
          this.queryInfo.pagesize = newSize
          this.getOrderList()
      },
      handleCurrentChange(newPage){
          this.queryInfo.pagenum = newPage
          this.getOrderList()
      },
      async showBox(){
        // const {data:res}  = await this.$http.get(`orders/${id}`)
        // if(res.meta.status !==200) return this.$message.error("")
        // this.addreeForm = res.data.consignee_addr
        this.editDialogVisible = true
      },
      addreeFormClose(){
          this.$refs.addreeFormRef.resetFields()
      },
      progesddClose(){
          this.$refs.addreeFormRef.resetFields()
      },
      handleChange(){
        //   console.log(val)
      },
      editForm(){
        //   console.log(this.addreeForm)
          this.editDialogVisible = false
      },
      addressDialogClosed() {
        this.$refs.addressFormRef.resetFields()
      },
     async showProgressBox() {
        // const { data: res } = await this.$http.get('/kuaidi/804909574412544580')
        // if (res.meta.status !== 200) {
        //     return this.$message.error('')
        // }
        this.progressInfo = progressInfo
        this.progressVisible = true
        // console.log(res)
        }
    }

}
</script>
<style lang="less" scoped>

.el-cascader {
  width: 100%;
}
</style>