<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        title=""
        type="info"
        show-icon
        center
        :closable="false"
      ></el-alert>
      <!--  -->
      <el-steps :space="200" :active="Number(activeIndex)" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!-- tag -->
      <el-form :model="addForm" ref="addFormRef" :rules="addFormRule" :tabel-width="100" label-position="top">
        <el-tabs v-model="activeIndex" tab-position="left" :before-leave='beforeTableLeave' @tab-click='taClickAdd'>
          <el-tab-pane label="" name="0">
            <!--  -->
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>

            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>

            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>

            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                  v-model="addForm.goods_cat"
                  :options="catelist"
                  :props="cate_prope"
                  @change="handleChange" clearable></el-cascader>
                </el-form-item>
                <!--  -->
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!-- item -->
            <el-form-item :label="item.attr_name" v-for="item in manyTableList" :key="item.attr_id">
               <!--  -->
               <el-checkbox-group v-model="item.attr_vals" >
                  <el-checkbox :label="it" v-for="(it,i) in item.attr_vals" :key="i" border	></el-checkbox>
               </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableList" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!-- actionAPI-->
            <el-upload
              :action="uploadURl"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headersObj"
              :on-success="handleSuccess">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <!--  -->
            <el-button type="primary" class="btnAbb" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%" >
      <img :src="previewPath" alt="" class="previewImg">
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data() {
    return {
      activeIndex: '0',
      // 
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // 
        goods_cat: [],
        // 
        pics:[],
        // 
        goods_introduce:'',
        attrs:[]
      },
      addFormRef: {},
       
      //
        catelist: [],
        cate_prope: {
          value: 'cat_id',
          label: 'cat_name',
          children: 'children',
          expandTrigger:'hover'
        },
        manyTableList:[],
        onlyTableList:[],
      // URL
        uploadURl:'http://127.0.0.1:8888/api/private/v1/upload',
        //headersheaderobj: {
        headersObj:{
          Authorization:
          window.sessionStorage.getItem( "token")
        },
        previewPath:'',
        previewVisible:false,
      // 
      addFormRule: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ],
      }
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    //   
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.catelist = res.data
      // console.log(this.catelist)
    },
    // ,
    handleChange() {
      if(this.addForm.goods_cat.length !==3){
        this.addForm.goods_cat = []
      }
    },
    beforeTableLeave(activeName, oldActiveName){
      if(oldActiveName === '0' && this.addForm.goods_cat.length !== 3){
        this.$message.error('')
        return false
      }
      return true
    },
    async taClickAdd(){
      // 
      if(this.activeIndex === '1'){
       const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:'many'}})
       if(res.meta.status !==200){
         return this.$message.error('')
       }
       res.data.forEach(item=>{
        item.attr_vals = item.attr_vals.length === 0? []:item.attr_vals.split(',')
       })
       this.manyTableList = res.data
      }else if(this.activeIndex === '2'){
       const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:'only'}})
       if(res.meta.status !==200){
         return this.$message.error('')
       }
       this.onlyTableList = res.data
      }
    },
    // 
    handlePreview(file){
      console.log(file)
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // 
    handleRemove(file){
      // 1
      const filePath = file.response.data.tmp_path
      // 2pics
      const i = this.addForm.pics.findIndex(x=>x.pic === filePath)
      //3splice,pics1
      this.addForm.pics.splice(i,1)
      console.log(this.addForm)
    },
    // 
    handleSuccess(response){
      // 1const picInfo = { pic:
      const picInfo = {pic:response.data.tmp_path }
      // 2push pics
      this.addForm.pics.push(picInfo)
      // console.log(response)
    },
    // 
    add(){
      this.$refs.addFormRef.validate(async val=>{
        if(!val){
          return this.$message.error('')
        }
        // 
        // lodashcloneDeep(obj) goods_cat
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        // 
        this.manyTableList.forEach(item=>{
          const newMay = {attr_id:item.attr_id,attr_value:item.attr_vals.join(' ')}
          this.addForm.attrs.push(newMay)
        })
        // 
        this.onlyTableList.forEach(item=>{
          const newOny = {attr_id:item.attr_id,attr_value:item.attr_vals}
          this.addForm.attrs.push(newOny)
        })
        form.attrs = this.addForm.attrs
        //
        //,
        const {data:res} = await this.$http.post(`goods`,form)
        if(res.meta.status !== 201){
          return this.$message.error(res.meta.msg)
        }
        this.$message.success(res.meta.msg)
        this.$router.push('/goods')
        // console.log(res)
      })
    }
    
  },
  computed:{
    cateId(){
      if(this.addForm.goods_cat.length === 3){
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>
<style lang="less" scoped>
.el-steps {
  margin: 10px;
}
.el-checkbox{
  margin: 5px 10px 0 0 !important;
}
.previewImg{
  width: 100%;
}
.btnAbb{
  margin: 10px;
}
</style>